* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0;
    height: 100vh;
    overflow: hidden;
    background: rgb(0, 0, 0);
}

.container {
    width: 400px;
    flex-direction: column;
    padding: 50px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    border-radius: 5px;
    border: 2px solid grey;
    background: rgb(42, 42, 42);
}

.header {
    margin: 10px;
    color: white;
    font-size: 18px;
    text-align: left;
}


.inputs {
    display: flex;
    flex-direction: column;
}

.inputs p {
    margin: 10px 0px 0 10px;
    width: 100%;
    color: rgb(202, 202, 202);
    padding-bottom: 8px;
}


.inputs input[type='text'] {
    margin: 10px;
    height: 40px;
    width: 100%;
    border: 1px solid #a3a3a3;
    border-radius: 5px;
    background: rgba(0, 0, 0, 0.1);
    color: #ffffff;
    text-align: left;
    padding-left: 10px;
    font-size: 16px;
}


.btns button {
    color: rgb(255, 255, 255);
    background: rgb(124, 124, 124);
    border: 3px solid rgb(124, 124, 124);
    border-radius: 5px;
    height: 40px;
    width: 100%;
    margin: 10px;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
}

.btns button:hover {
    background-color: rgb(0, 0, 0);
}


.results {
    display: flex;
    flex-direction: column;
}

.results p,
.results h3 {
    margin: 10px 0px 0 10px;
    width: 100%;
    color: rgb(202, 202, 202);
    padding-bottom: 8px;
}

